import * as React from 'react';
import InfoProps from '../props/InfoProps';

export default class Info extends React.Component<InfoProps> {
  constructor(props: InfoProps) {
    super(props);
  }

  render(): JSX.Element {
    return (
      <div className="info">
        <p> 3={this.props.info.book3} 4={this.props.info.book4} 5={this.props.info.book5}</p>
        <p> {this.props.info.medalCount} (15{this.props.info.evolCount})</p>
        <p> ={this.props.info.budCount} ={this.props.info.flowerCount}</p>
        <p> {this.props.info.scoutCost}</p>
        <p> {this.props.info.levelUpCost}</p>
      </div>
    );
  }
}